      font-family: Nexa Bold!important;
  } */
  
.check-block {
    display: flex;
    justify-content: space-between;
}
  
.check-effect:not(:checked),
.check-effect:checked{
    position: absolute;
    left: -9999px;
}
  
.check-effect-1:not(:checked) + label:before,
.check-effect-1:checked + label:before {
      content: "";
      display: inline-block;
      width: 16px;
      height: 16px;
      border: 1.5px solid #CCCCCC;
      box-sizing: border-box;
      border-radius: 3px;
      margin-right:5px;
      position: relative;
      top: -0px;
  }
  
  .check-effect-1:not(:checked) + label:after,
 .check-effect-1:checked + label:after {
    content: "\f00c";
    font-family: FontAwesome;
    font-size: 11px;
    display: flex;
    height: 100%;
    align-items:center;
    color: #fff;
    transform-origin: center center;
    position: absolute;
    top:-2.5px;
    left:2px;
}
  
  .check-effect-1:not(:checked) + label:after {
    transform: scale(0);
  }
  
  .check-effect-1:checked + label:before {
    background-color: #f8cf2c;
    border-color: #f8cf2c;
    transition: all 0.2s delay-0s;
  }
  
  .check-effect-1:checked + label:after {
    transform: scale(1);
    transition: all 0.2s delay-0.2s;
  }
  .check-1 label , .check-2 label{
    font-family:"Nexa"!important;
    font-style: normal;
    font-weight:normal!important;
    font-size: 14px;
    color: #545454;
    cursor: pointer;
    position: relative;
    display: flex;
    justify-content:baseline;
  }
  .pg-3  label{
